.sidebar {
  ul {
    list-style: none;
    /* Remove default list styles */
    padding: 0;
    /* Remove default padding */
    margin: 0;

    /* Remove default margin */
    li {
      margin-bottom: 20px;

      /* Add margin between list items */
      a {
        display: block;
        /* Display links as block elements */
      }
    }
  }
}


/*Top content*/
.top-container {
  position: fixed;
  top: 5%;
  left: 10%;

}

.small-box {
  margin-bottom: 30px;
}

/*Main Content*/
h1,
h2,
h3,
h4 {
  color: #578F8C !important;
}

.main-container {
  position: relative;
  top: 80px;
  margin-top: 10%;
  /* right: 100px; */
}

